<template>
  <div id="Amazonwithdrawal">
    <h1>亚马逊提款</h1>
    <div class="topOption">
      <el-form
        :inline="true"
        ref="withdrawlForm"
        :rules="rules"
        :model="formData"
      >
        <el-form-item prop="platform " label="渠道">
          <el-select v-model="formData.platform">
            <el-option
              v-for="(item, index) in channels"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="site   " label="站点">
          <el-select @change="changeStation" v-model="formData.site">
            <el-option
              v-for="(item, index) in stations"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="country" label="国家">
          <el-select @change="changeCountry" v-model="formData.country">
            <el-option
              v-for="(item, index) in countries"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="currency" label="提款币种">
          <el-input v-model="formData.currency" readonly></el-input>
        </el-form-item>
        <el-form-item prop="amount" label="提款金额">
          <el-input v-model="formData.amount"></el-input>
        </el-form-item>
        <el-form-item label="p卡尾号" prop="payment_account">
          <el-input v-model="formData.payment_account"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitData('withdrawlForm')"
            >确 认</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      countryItem: [],
      formData: {
        platform: "", // platform
        site: "", //site
        country: "",
        currency: "",
        payment_account: "",
        amount: ""
      },
      channels: [],
      stations: [],
      countries: [],
      rules: {
        platform: [
          {
            required: true,
            trigger: "blur",
            message: "请选择渠道"
          }
        ],
        site: [{ required: true, message: "请选择站点", trigger: "blur" }],
        country: [{ required: true, message: "请选择国家", trigger: "blur" }],
        currency: [
          { required: true, message: "请输入提款币种", trigger: "blur" }
        ],
        payment_account: [
          { required: true, message: "请输入p卡尾号", trigger: "blur" }
        ],
        amount: [
          { required: true, message: "请输入提款金额", trigger: "blur" },
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ]
      },
      item: "",
      AllMenu: ""
    };
  },
  methods: {
    submitData(FormName) {
      this.$refs[FormName].validate(valid => {
        if (!valid) return;
        // 发送请求
        let url = this._url2 + "server/databases/withdrawal_page/";
        let fd = new FormData();
        for (let key in this.formData) {
          fd.append(key, this.formData[key]);
        }
        this.$axios.post(url, fd).then(res => {
          if (res.data.code === 200) {
            this.$message.success("请求成功");
            this.formData.amount = "";
            this.formData.payment_account = "";
          } else {
            this.$message.warning("请求失败");
          }
        });
      });
    },
    getMenu() {
      let url = this._url2 + "server/databases/withdrawal_page/";
      this.$axios.get(url).then(res => {
        console.log(res);
        this.AllMenu = res.data.re_data || {};
        // 渠道
        this.channels = Object.keys(this.AllMenu) || [];
        // this.formData.platform  =
        //   this.channels.length > 0 ? this.channels[0] : "";
        // 站点
        this.item = this.AllMenu["Amazon"] || {};
        this.stations = Object.keys(this.item);
      });
    },
    changeCountry(val) {
      this.formData.currency = "";
      let item = this.countryItem.find(
        i => i.country === this.formData.country
      );
      console.log(item);
      this.formData.currency = item.currency;
    },
    changeStation(val) {
      this.countries = [];
      this.formData.country = "";
      this.formData.currency = "";
      console.log(val, this.item);
      for (let key in this.item) {
        if (key === val) {
          this.countryItem = this.item[key];
          console.log(this.countryItem);

          for (let i in this.countryItem) {
            let item = this.countryItem[i];
            this.countries.push(item.country);
          }
        }
      }
    }
  },
  created() {
    this.getMenu();
  }
};
</script>

<style scoped>
#Amazonwithdrawal {
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
  padding: 22px 0;
}
#Amazonwithdrawal h1 {
  margin-bottom: 22px;
}
</style>



